window.toggleAbstractDisplayPubPage = function(id) {
    const el = document.getElementById(id);
    const unfold = el.getAttribute('unfold');
    if (unfold && unfold === 'true') {
        el.style['max-height'] = '0';
        el.setAttribute('unfold', 'false');
    } else {
        const p = el.querySelector('p');
        el.style['max-height'] = p.getBoundingClientRect().height + 'px';
        el.setAttribute('unfold', 'true');
    }
}

new Vue({
    el: '#member-timeline',
    mounted() {
        this.$el.querySelectorAll('.el-timeline-item__content')
            .forEach(el => {
                el.classList.add('row')
            })
        this.$el.querySelectorAll('.el-timeline-item__wrapper')
            .forEach(el => {
                el.style.width = '100%'
            })
        this.$el.querySelectorAll('.el-timeline-item__node')
            .forEach(el => {
                el.style.height = el.style.width = '20px'
                el.style.left = '-4px'
            })
        this.$el.querySelectorAll('.el-timeline-item__tail')
            .forEach(el => {
                el.style['border-left-width'] = '4px'
            })
        this.$el.querySelectorAll('.el-timeline-item__timestamp')
            .forEach(el => {
                el.classList.add('h3')
                el.style['font-size'] = '2rem'
                el.style.transform = 'translate(0,-10px)'
            })
    }
})

$(function () {
    /* =========================================
     *  Member img lazy load
     *  =======================================*/
    imgLazyLoad();
});

function imgLazyLoad() {
    function throttle(func, delay, atLeast) {
        let timeout = null, startTime = new Date()
        return function () {
            const curTime = new Date()
            clearTimeout(timeout);
            if (curTime - startTime >= atLeast) {
                func()
                startTime = curTime
            } else {
                timeout = setTimeout(func, delay)
            }
        }
    }

    function lazyLoad() {
        const images = document.getElementsByClassName('lazy-load-img')
        const len = images.length;
        let n = 0;
        return function () {
            // const seeHeight = document.documentElement.clientHeight;
            // const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            for (let i = n; i < len; i++) {
                // if (images[i].getBoundingClientRect().y < seeHeight + scrollTop) {
                    if (images[i].getAttribute('src') === 'img/loading.gif') {
                        images[i].src = images[i].getAttribute('data-src')
                    }
                    n = n + 1
                // }
            }
        }
    }

    const loadImages = lazyLoad();
    loadImages();
    window.addEventListener('scroll', throttle(loadImages, 500, 1000), false);
}
